import { useEffect, useState } from 'react';
import { StarIcon } from '@heroicons/react/20/solid';
import { RadioGroup } from '@headlessui/react';
import { useDispatch, useSelector } from 'react-redux';
import {
    fetchProductByIdAsync,
    selectProductById,
} from '../../product/productSlice';
import { useParams } from 'react-router-dom';
import {
    addToCartAsync,
    selectItems,
    selectStatus,
} from '../../cart/cartSlice';
import toast from 'react-hot-toast';
import { Grid } from 'react-loader-spinner';

function classNames(...classes) {
    return classes.filter(Boolean).join(' ');
}

export default function AdminProductDetails() {
    const dispatch = useDispatch();
    const { id } = useParams();
    const items = useSelector(selectItems);
    const product = useSelector(selectProductById);
    const [selectedColor, setSelectedColor] = useState('');
    const [selectedSize, setSelectedSize] = useState('');
    const status = useSelector(selectStatus);
    function handleCart(e) {
        e.preventDefault();
        if (items.findIndex((item) => item.product.id === product.id) < 0) {
            const newItem = {
                product: product.id,
                quantity: 1,
            };
            if (selectedColor) newItem.color = selectedColor;
            if (selectedSize) newItem.size = selectedSize;
            dispatch(addToCartAsync(newItem));
        } else {
            toast.error('Already added to cart');
        }
    }

    useEffect(() => {
        dispatch(fetchProductByIdAsync(id));
    }, [dispatch, id]);

    return (
        <div className="bg-white">
            {status === 'loading' ? (
                <div className="flex items-center justify-center h-screen w-[100vw]">
                    {' '}
                    <Grid
                        height="80"
                        width="80"
                        color="#1F2937"
                        ariaLabel="grid-loading"
                        radius="12.5"
                        wrapperStyle={{}}
                        wrapperClass=""
                        visible={true}
                    />
                </div>
            ) : (
                product && (
                    <div className="pt-6">
                        {/* Product info */}
                        <div className="mx-auto max-w-2xl px-4 pb-16 pt-10 sm:px-6 lg:grid lg:max-w-7xl lg:grid-cols-3 lg:grid-rows-[auto,auto,1fr] lg:gap-x-8 lg:px-8 lg:pb-24 lg:pt-16">
                            <div className="lg:col-span-2 lg:border-r lg:border-gray-200 lg:pr-8">
                                <h1 className="text-2xl font-bold tracking-tight text-gray-900 sm:text-3xl">
                                    {product.title}
                                </h1>
                            </div>
                        </div>

                        {/* Image gallery */}
                        <div className="mx-auto mt-6 max-w-2xl sm:px-6 lg:grid lg:max-w-7xl lg:grid-cols-3 lg:gap-x-8 lg:px-8">
                            <div className="aspect-h-4 aspect-w-3 hidden overflow-hidden rounded-lg lg:block">
                                <img
                                    src={product?.images[0]}
                                    alt={product?.images[0]}
                                    className="h-full w-full object-cover object-center"
                                />
                            </div>
                            <div className="hidden lg:grid lg:grid-cols-1 lg:gap-y-8">
                                <div className="aspect-h-2 aspect-w-3 overflow-hidden rounded-lg">
                                    <img
                                        src={product.images[1]}
                                        alt={product.images[1]}
                                        className="h-full w-full object-cover object-center"
                                    />
                                </div>
                                <div className="aspect-h-2 aspect-w-3 overflow-hidden rounded-lg">
                                    <img
                                        src={product.images[2]}
                                        alt={product.images[2]}
                                        className="h-full w-full object-cover object-center"
                                    />
                                </div>
                            </div>
                            <div className="aspect-h-5 aspect-w-4 lg:aspect-h-4 lg:aspect-w-3 sm:overflow-hidden sm:rounded-lg">
                                <img
                                    src={product.images[3]}
                                    alt={product.images[3]}
                                    className="h-full w-full object-cover object-center"
                                />
                            </div>
                        </div>

                        {/* Product info */}
                        <div className="mx-auto max-w-2xl px-4 pb-16 pt-10 sm:px-6 lg:grid lg:max-w-7xl lg:grid-cols-3 lg:grid-rows-[auto,auto,1fr] lg:gap-x-8 lg:px-8 lg:pb-24 lg:pt-16">
                            <div className="lg:col-span-2 lg:border-r lg:border-gray-200 lg:pr-8">
                                <h1 className="text-2xl font-bold tracking-tight text-gray-900 sm:text-3xl">
                                    {product.title}
                                </h1>
                            </div>

                            {/* Options */}
                            <div className="mt-4 lg:row-span-3 lg:mt-0">
                                <h2 className="sr-only">Product information</h2>
                                <p className="text-3xl font-medium text-gray-900">
                                    ${product.discountPrice}
                                </p>
                                <p className="text-sm tracking-tight text-gray-900 line-through">
                                    ${product.price}
                                </p>
                                {/* Reviews */}
                                <div className="mt-6">
                                    <h3 className="sr-only">Reviews</h3>
                                    <div className="flex items-center">
                                        <div className="flex items-center">
                                            {[0, 1, 2, 3, 4].map((rating) => (
                                                <StarIcon
                                                    key={rating}
                                                    className={classNames(
                                                        product.rating > rating
                                                            ? 'text-gray-900'
                                                            : 'text-gray-200',
                                                        'h-5 w-5 flex-shrink-0'
                                                    )}
                                                    aria-hidden="true"
                                                />
                                            ))}
                                        </div>
                                        <p className="sr-only">
                                            {product.rating} out of 5 stars
                                        </p>
                                    </div>
                                </div>

                                <form className="mt-10">
                                    {/* Colors */}
                                    {product.colors &&
                                        product.colors.length > 0 && (
                                            <div>
                                                <h3 className="text-sm font-medium text-gray-900">
                                                    Color
                                                </h3>

                                                <RadioGroup
                                                    value={selectedColor}
                                                    onChange={setSelectedColor}
                                                    className="mt-4">
                                                    <RadioGroup.Label className="sr-only">
                                                        Choose a color
                                                    </RadioGroup.Label>
                                                    <div className="flex items-center space-x-3">
                                                        {product.colors &&
                                                            product.colors.map(
                                                                (color) => (
                                                                    <RadioGroup.Option
                                                                        key={
                                                                            color.id
                                                                        }
                                                                        value={
                                                                            color.id
                                                                        }
                                                                        className={({
                                                                            active,
                                                                            checked,
                                                                        }) =>
                                                                            classNames(
                                                                                color.selectedClass,
                                                                                active &&
                                                                                    checked
                                                                                    ? 'ring ring-offset-3'
                                                                                    : '',
                                                                                !active &&
                                                                                    checked
                                                                                    ? 'ring-2'
                                                                                    : '',
                                                                                'relative -m-0.5 flex cursor-pointer items-center justify-center rounded-full p-0.5 focus:outline-none'
                                                                            )
                                                                        }>
                                                                        <RadioGroup.Label
                                                                            as="span"
                                                                            className="sr-only">
                                                                            {
                                                                                color.name
                                                                            }
                                                                        </RadioGroup.Label>
                                                                        <span
                                                                            aria-hidden="true"
                                                                            className={classNames(
                                                                                color.class,
                                                                                'h-8 w-8 rounded-full border border-black border-opacity-10'
                                                                            )}
                                                                        />
                                                                    </RadioGroup.Option>
                                                                )
                                                            )}
                                                    </div>
                                                </RadioGroup>
                                            </div>
                                        )}

                                    {/* Sizes */}
                                    {product.sizes &&
                                        product.sizes.length > 0 && (
                                            <div className="mt-10">
                                                <div className="flex items-center justify-between">
                                                    <h3 className="text-sm font-medium text-gray-900">
                                                        Size
                                                    </h3>
                                                    <a
                                                        href="#"
                                                        className="text-sm font-medium text-indigo-600 hover:text-indigo-500">
                                                        Size guide
                                                    </a>
                                                </div>

                                                <RadioGroup
                                                    value={selectedSize}
                                                    onChange={setSelectedSize}
                                                    className="mt-4">
                                                    <RadioGroup.Label className="sr-only">
                                                        Choose a size
                                                    </RadioGroup.Label>
                                                    <div className="grid grid-cols-4 gap-4 sm:grid-cols-8 lg:grid-cols-4">
                                                        {product.sizes &&
                                                            product.sizes.map(
                                                                (size) => (
                                                                    <RadioGroup.Option
                                                                        key={
                                                                            size.id
                                                                        }
                                                                        value={
                                                                            size.id
                                                                        }
                                                                        disabled={
                                                                            !size.inStock
                                                                        }
                                                                        className={({
                                                                            active,
                                                                        }) =>
                                                                            classNames(
                                                                                size.inStock
                                                                                    ? 'cursor-pointer bg-white text-gray-900 shadow-sm'
                                                                                    : 'cursor-not-allowed bg-gray-50 text-gray-200',
                                                                                active
                                                                                    ? 'ring-2 ring-indigo-500'
                                                                                    : '',
                                                                                'group relative flex items-center justify-center rounded-md border py-3 px-4 text-sm font-medium uppercase hover:bg-gray-50 focus:outline-none sm:flex-1 sm:py-6'
                                                                            )
                                                                        }>
                                                                        {({
                                                                            active,
                                                                            checked,
                                                                        }) => (
                                                                            <>
                                                                                <RadioGroup.Label as="span">
                                                                                    {
                                                                                        size.name
                                                                                    }
                                                                                </RadioGroup.Label>
                                                                                {size.inStock ? (
                                                                                    <span
                                                                                        className={classNames(
                                                                                            active
                                                                                                ? 'border'
                                                                                                : 'border-2',
                                                                                            checked
                                                                                                ? 'border-indigo-500'
                                                                                                : 'border-transparent',
                                                                                            'pointer-events-none absolute -inset-px rounded-md'
                                                                                        )}
                                                                                        aria-hidden="true"
                                                                                    />
                                                                                ) : (
                                                                                    <span
                                                                                        aria-hidden="true"
                                                                                        className="pointer-events-none absolute -inset-px rounded-md border-2 border-gray-200">
                                                                                        <svg
                                                                                            className="absolute inset-0 h-full w-full stroke-2 text-gray-200"
                                                                                            viewBox="0 0 100 100"
                                                                                            preserveAspectRatio="none"
                                                                                            stroke="currentColor">
                                                                                            <line
                                                                                                x1={
                                                                                                    0
                                                                                                }
                                                                                                y1={
                                                                                                    100
                                                                                                }
                                                                                                x2={
                                                                                                    100
                                                                                                }
                                                                                                y2={
                                                                                                    0
                                                                                                }
                                                                                                vectorEffect="non-scaling-stroke"
                                                                                            />
                                                                                        </svg>
                                                                                    </span>
                                                                                )}
                                                                            </>
                                                                        )}
                                                                    </RadioGroup.Option>
                                                                )
                                                            )}
                                                    </div>
                                                </RadioGroup>
                                            </div>
                                        )}
                                    <button
                                        type="submit"
                                        onClick={handleCart}
                                        className="mt-10 flex w-full items-center justify-center rounded-md border border-transparent bg-indigo-600 px-8 py-3 text-base font-medium text-white hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2">
                                        Add to Cart
                                    </button>
                                </form>
                            </div>

                            <div className="py-10 lg:col-span-2 lg:col-start-1 lg:border-r lg:border-gray-200 lg:pb-16 lg:pr-8 lg:pt-6">
                                {/* Description and details */}
                                <div>
                                    <h3 className="sr-only">Description</h3>

                                    <div className="space-y-6">
                                        <p className="text-base text-gray-900">
                                            {product.description}
                                        </p>
                                    </div>
                                </div>

                                {product.highlights &&
                                    product.highlights.length > 0 && (
                                        <div className="mt-10">
                                            <h3 className="text-sm font-medium text-gray-900">
                                                Highlights
                                            </h3>

                                            <div className="mt-4">
                                                <ul
                                                    role="list"
                                                    className="list-disc space-y-2 pl-4 text-sm">
                                                    {product.highlights &&
                                                        product.highlights.map(
                                                            (
                                                                highlight,
                                                                idx
                                                            ) => (
                                                                <li
                                                                    key={
                                                                        highlight ||
                                                                        idx
                                                                    }
                                                                    className="text-gray-400">
                                                                    <span className="text-gray-600">
                                                                        {
                                                                            highlight
                                                                        }
                                                                    </span>
                                                                </li>
                                                            )
                                                        )}
                                                </ul>
                                            </div>
                                        </div>
                                    )}

                                <div className="mt-10">
                                    <h2 className="text-sm font-medium text-gray-900">
                                        Details
                                    </h2>

                                    <div className="mt-4 space-y-6">
                                        <p className="text-sm text-gray-600">
                                            {product.description}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                )
            )}
        </div>
    );
}
